.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar__inner {
  height: 100%;
  width: 100%;
  border: 1px solid #313131;
  border-radius: 12px;
  background-color: #c3b4f3;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill {
  background-color: #4826b9;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  font-weight: bold;
  font-size: 0.5rem;
  text-align: center;
}

@media (max-width: 786px) {
  .chart-bar {
    display: grid;
    grid-template-columns: 5fr 1fr;
    margin-block-end: 2%;
  }

  .chart-bar .chart-bar__inner {
    margin-right: 5%;
  }

  .chart-bar__inner {
    margin: 2%;
  }
  .chart-bar__label {
    font-size: small;
  }
}
@media (min-width: 1460px) and (max-width: 1960px) {
  .chart-bar__inner{
    width: 2rem;
    border: 1px solid #9f86c0;
  }
}